import { useState } from 'react';

export const useVideoHandler = (editor) => {
  const [isVideoModalOpen, setIsVideoModalOpen] = useState(false);

  const handleVideoClick = () => {
    setIsVideoModalOpen(true);
  };

  const handleVideoSubmit = (videoUrl) => {
    if (!videoUrl) {
      return;
    }

    // ë” í¬ê´„ì ì¸ YouTube URL ì •ê·œì‹
    const youtubeRegex = /(?:https?:\/\/)?(?:www\.|m\.|music\.)?(?:youtube\.com\/(?:watch\?v=|embed\/|v\/|.+\?v=)|youtu\.be\/|youtube\.com\/shorts\/)([a-zA-Z0-9_-]{11})/;

    const match = videoUrl.match(youtubeRegex);

    if (match) {
      const videoId = match[1];
      const embedUrl = `https://www.youtube.com/embed/${videoId}`;

      const result = editor.chain().focus().setYouTubeVideo({
        src: embedUrl,
        width: 560,
        height: 315,
      }).run();

      if (!result) {
        alert('YouTube ì˜ìƒ ì‚½ì…ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤. URLì„ í™•ì¸í•´ì£¼ì„¸ìš”.');
      }
    } else {
      alert('ì˜¬ë°”ë¥¸ YouTube URLì„ ì…ë ¥í•´ì£¼ì„¸ìš”. ì˜ˆ: https://www.youtube.com/watch?v=LmZD-TU96q4');
    }

    setIsVideoModalOpen(false);
  };

  const closeVideoModal = () => setIsVideoModalOpen(false);

  return {
    isVideoModalOpen,
    handleVideoClick,
    handleVideoSubmit,
    closeVideoModal
  };
};